<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body p-0">
        <div class="row">
          <div class="col-lg-9 col-md-6 col-sm-6 col-6">
            <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
          </div>
          <div class="col-lg-3 col-md-6 col-sm-6 col-6">
            <column-toggle
              v-model="columnDefs"
              @input="cols => this.$refs['table'].updateColumns(cols)"
            ></column-toggle>
          </div>
        </div>
        <div class="col-sm-12 p-0">
          <my-table
            ref="table"
            :columnDefs="columnDefs"
            v-bind:data-rows="customers"
            :actions="actions"
            @sortChange="sortChange"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import customerSchemas from './customer-schemas'
  import dtHelper from 'src/helpers/datatable'
  import ColumnToggle from 'src/components/UIComponents/ColumnToggle'

  export default{
    components: {
      MyTable,
      MyFilter,
      ColumnToggle
    },
    computed: {
      customers() {
        let rows = this.$store.state.customers;
        return dtHelper.filterByRules(rows, this.filterOutput);
      }
    },
    data () {
      let initFiledArrays = ['id', 'name', 'email', 'phone', 'address', 'created_at'];
      let columnDefs = dtHelper.buildInitFields(customerSchemas, initFiledArrays);

      return {
        filterOutput: [],
        columnDefs: columnDefs,
        actions: [
          {
            type: 'primary',
            icon: 'nc-icon nc-ruler-pencil',
            callback: this.edit
          }
        ],
        filter: {},
        rules: dtHelper.buildRules(customerSchemas)
      }
    },
    mounted() {
      this.$store.dispatch('fetchCustomers');

      this.$store.dispatch('setPageTitle', 'khách hàng');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Tạo khách hàng',
        type: 'primary',
        icon: '',
        callback: this.create
      }])
    },
    methods: {
      create(){
        this.$router.push('/customer/create');
      },
      sortChange(data){
        let prop = data.prop;
        let order = data.order == 'ascending' ? 'asc' : 'desc';
        this.$store.dispatch('fetchCustomers', {
          order: prop + '=' + order
        });
      },
      edit(index, row){
        this.$router.push('/customer/' + row.id);
      },
      updateFilter(filterOutput) {
        this.filterOutput = filterOutput;
      },
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
  button.btn {
    margin-right: 5px;
  }
  }
</style>
